import React from "react";
import "./ImageSelectionPage.css";
import PochiImage from "./assets/images/pochi.jpg";
import bibiImage from "./assets/images/IMG_9705.JPG";
import valImage from "./assets/images/val.jpg";
import test2Image from "./assets/images/test22.png";

const gamePoster = {
  title: "게임 포스터",
  description: "이 게임은 너무 재미있어서 세상에 맙소사가 되었습니다.",
  imageUrl: valImage,
};

const developers = [
  {
    id: 1,
    name: "Redping",
    greeting: "안녕하세요, 프로젝트 매니저입니다.",
    imageUrl: PochiImage,
  },
  {
    id: 2,
    name: "Toto",
    greeting: "안녕하세요, 개발자입니다.",
    imageUrl: test2Image,
  },
  {
    id: 3,
    name: "bibi",
    greeting: "안녕하세요, 디자이너입니다.",
    imageUrl: bibiImage,
  },
  // Add more developers and their details
];
const ImageSelectionPage = () => {
  return (
    <div className="image-selection-page">
      <div className="main-image-container fade-in">
        {/* //<img src={gamePoster.imageUrl} alt={gamePoster.title} /> */}

        <h1 className="typing-title">{gamePoster.title}</h1>
        <p>{gamePoster.description}</p>
      </div>
      <div className="view-all-projects fade-in">모든 프로젝트 보러 가기</div>
      <div className="image-list">
        {developers.map((developer) => (
          <div key={developer.id} className="developer-container fade-in">
            <img src={developer.imageUrl} alt={developer.name} />
            <h2>{developer.name}</h2>
            <div className="greeting-container">
              <p>{developer.greeting}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ImageSelectionPage;
